.loginPanel {
  margin-top: -30px;
}
.content-container {
  position: relative;
  height: 100%;
  background-color: #fff;
}
.content-container .ant-tabs-tabpane {
  position: relative;
  height: calc(100vh - 104px);
  overflow: auto;
}
.content-container .bg-box {
  position: 'absolute';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #eee;
}
.content-container .bg-size {
  width: 100%;
  height: calc(100vh - 64px);
  overflow: hidden;
}
.content-container .tabpane-box {
  min-height: calc(100vh - 173px);
}
.my-sider {
  position: relative;
  z-index: 10;
  height: 100%;
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
}
.my-sider.light {
  box-shadow: 2px 0 8px 0 rgba(29, 35, 41, 0.05);
}
.my-sider .ant-menu-light {
  border-right-color: transparent;
}
.my-sider .sider-menu-logo {
  position: relative;
  height: 64px;
  padding-left: 24px;
  overflow: hidden;
  line-height: 64px;
  background: #001529;
  transition: all 0.3s;
}
.my-sider .sider-menu-logo.light {
  background: #fff;
  box-shadow: 1px 1px 0 0 #e8e8e8;
}
.my-sider .sider-menu-logo.light h1 {
  color: #1890ff;
}
.my-sider .sider-menu-logo img {
  display: inline-block;
  height: 32px;
  vertical-align: middle;
}
.my-sider .sider-menu-logo h1 {
  display: inline-block;
  margin: 0 0 0 12px;
  color: #fff;
  font-weight: 600;
  font-size: 20px;
  font-family: Avenir, Helvetica Neue, Arial, Helvetica, sans-serif;
  vertical-align: middle;
}
.logo {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo img {
  display: block;
  height: 25px;
  width: 127px;
}
.ant-layout-sider-dark {
  background-color: #f3f4f5 !important;
}
.top-header {
  background: #42464d;
  padding: 0 15 0 0px;
  position: relative;
}
.top-header .top-header-inner {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.top-header .top-header-inner .headre-left {
  width: 220px;
}
.top-header .top-header-inner .header-title {
  flex: 1;
  padding-left: 15px;
  font-size: 20px;
}
.top-header .top-header-inner .header-center {
  flex: 1;
  width: 100px;
  height: 60px;
  line-height: 60px;
}
.top-header .top-header-inner .header-right {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 320px;
}
.top-header .top-header-inner .header-right .header-name {
  font-size: 16px;
  font-family: NotoSansCJKsc-Regular, NotoSansCJKsc;
  font-weight: 400;
  color: #ffffff;
  line-height: 24px;
}
.top-header .top-header-inner .header-right .personal-center {
  font-size: 14px;
  font-family: NotoSansCJKsc-Light, NotoSansCJKsc;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.6);
  line-height: 20px;
}
.top-header .top-header-inner .header-right .leave {
  font-size: 14px;
  font-family: NotoSansCJKsc-Light, NotoSansCJKsc;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.6);
  line-height: 20px;
}
.top-header .top-header-inner .header-right > div {
  margin-left: 20px;
}
.top-header .drop-list li {
  padding: 10px 15px;
}
.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
  border-right: none !important;
}
.ant-menu-vertical .ant-menu-item::after,
.ant-menu-vertical-left .ant-menu-item::after,
.ant-menu-vertical-right .ant-menu-item::after,
.ant-menu-inline .ant-menu-item::after {
  border-right: 0 !important;
}
.notice-tips {
  position: absolute;
  right: -20px;
  top: -5px;
  display: inline-block;
  width: 15px;
  height: 15px;
  background: red;
  border-radius: 15px;
  text-align: center;
  line-height: 15px;
  font-size: 12px;
  color: #fff;
}
.ant-tabs-nav-container {
  line-height: 2.6;
  color: rgba(0, 0, 0, 0.65);
  border-bottom: 0;
}
.collapse-center {
  height: 30px;
  text-align: center;
  width: 100%;
  margin-top: 10px;
}
.collapse-right {
  height: 30px;
  text-align: right;
  padding-right: 26px;
  width: 100%;
  margin-top: 22px;
}
.ant-menu-vertical .ant-menu-item::after,
.ant-menu-vertical-left .ant-menu-item::after,
.ant-menu-vertical-right .ant-menu-item::after,
.ant-menu-inline .ant-menu-item::after {
  position: absolute;
  top: 0;
  left: 0 !important;
  bottom: 0;
  -webkit-transform: scaleY(0.0001);
  -ms-transform: scaleY(0.0001);
  transform: scaleY(0.0001);
  -webkit-transition: opacity 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: opacity 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: transform 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), opacity 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: transform 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), opacity 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
  content: '';
}
.app-sider {
  min-height: calc(100vh - 60px) !important;
  height: 100px !important;
}
.ant-tabs-ink-bar {
  bottom: 0;
  height: 0 !important;
  position: static !important;
}
.ant-menu-root.ant-menu-vertical,
.ant-menu-root.ant-menu-vertical-left,
.ant-menu-root.ant-menu-vertical-right,
.ant-menu-root.ant-menu-inline {
  min-height: calc(100vh - 60px);
}
.ant-tabs-bar {
  font-size: 16px;
  font-family: NotoSansCJKsc-Light, NotoSansCJKsc;
  border: none !important;
}
.ant-tabs-tab {
  font-size: 16px;
  font-family: NotoSansCJKsc-Light, NotoSansCJKsc;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.6);
  line-height: 24px;
}
.ant-dropdown-trigger {
  width: 80px;
}
.ant-avatar-lg {
  margin-right: 5px;
  width: 30px !important;
  height: 30px !important;
}
.ant-layout-content {
  box-sizing: border-box;
}
.ant-table-thead > tr > th {
  height: 48px !important;
  background: #f8f8f8 !important;
  font-size: 12px;
  font-family: NotoSansCJKsc-Regular, NotoSansCJKsc;
  font-weight: 400;
  color: #646f73 !important;
  line-height: 18px;
}
.ant-table-tbody > tr > td {
  max-width: 110px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.ant-modal-footer {
  display: flex;
}
.ant-modal-title {
  font-size: 20px;
  font-weight: bolder;
}
.ant-tabs-nav .ant-tabs-tab-active {
  border-bottom: 4px solid #00bbf8;
  color: #fff;
  height: 60px;
  line-height: 30px;
}
.statistics {
  position: relative;
  width: 100%;
  height: 100%;
}
.statistics img {
  width: 100%;
  height: auto;
}
.statistics .closebtn {
  position: absolute;
  left: 2%;
  top: 6%;
  width: 120px;
  height: 40px;
  cursor: pointer;
}
.tabs {
  margin-right: 50px;
  padding: 12px 16px;
  color: hsla(0, 0%, 100%, 0.6);
  cursor: pointer;
}
.tabs:hover {
  color: #2cbde6;
}
.tabs-active {
  border-bottom: 4px solid #00bbf8;
}
.chinaMap {
  position: absolute;
  left: 30%;
  top: 20%;
  width: 600px;
  height: 600px;
  z-index: 200;
}
.statistics-modal .ant-modal-content .ant-modal-close {
  display: block !important;
}
