html {
  overflow-x: hidden;
  overflow-y: auto;
}

body {
  width: 100vw;
  /*height: auto;*/
  overflow: auto;
  padding-left: calc(100vw - 100%);
}

#app {
  width: 100%;
  height: 100%;
}

@font-face {
  font-family: 'iconfont';  /* project id 1708186 */
  src: url('//at.alicdn.com/t/font_1708186_5azdwzdbi7s.eot');
  src: url('//at.alicdn.com/t/font_1708186_5azdwzdbi7s.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_1708186_5azdwzdbi7s.woff2') format('woff2'),
  url('//at.alicdn.com/t/font_1708186_5azdwzdbi7s.woff') format('woff'),
  url('//at.alicdn.com/t/font_1708186_5azdwzdbi7s.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_1708186_5azdwzdbi7s.svg#iconfont') format('svg');
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 18px;
  font-style: normal;
  margin: 0 3px;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.markdown {
  color: #314659;
  font-size: 14px;
  line-height: 2;
}

.markdown ol {
  list-style: circle inside;
}

.markdown code {
  margin: 0 1px;
  padding: .2em .4em;
  font-size: .9em;
  background: #f2f4f5;
  border: 1px solid #eee;
  border-radius: 3px;
}

.markdown h2,
.markdown h3,
.markdown h4,
.markdown h5,
.markdown h6 {
  clear: both;
  /* margin: 1.6em 0 .6em; */
  color: #0d1a26;
  font-weight: 500;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
}

.markdown h3 {
  font-size: 18px;
}

.primary-color {
  color: var(--primaryColor);
}

.my-a {
  color: var(--primaryColor);
  cursor: pointer;
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}